<template>
  <div class="error">{{ message }}</div>
</template>

<script>
export default {
  name: "ShowError",
  props: {
    message: String
  }
}
</script>

<style>
  .error {
    position: fixed;
    display: flex;
    border-radius: 8px;
    font-size: 20px;
    align-items: center;
    justify-content: center;
    width: 600px;
    height: 400px;
    top: calc(50% - 200px);
    left: calc(50% - 300px);
    background-color: red;
    color: white;
  }
</style>