export default {
  data() {
    return {
      ws: null,
      isWsOpen: false
    }
  },
  methods: {
    capitalizeFirstLetter(s) {
      if (typeof s !== "string") return ""
      return s.charAt(0).toUpperCase() + s.slice(1)
    },
    connect() {
      const host = location.origin.replace(/^http/, 'ws')
      // const host = "ws://localhost:8081"
      const ws = new WebSocket(host)
      ws.onopen = this.onWsOpen
      ws.onerror = this.onWsError
      ws.onmessage = this.onWsMessage
      this.ws = ws
    },
    onWsOpen() {
      this.isWsOpen = true
    },
    onWsError(error) {
      console.error("error on ws: ", error)
    },
    onWsMessage(message) {
      try {
        const response = JSON.parse(message.data);
        console.log({ response })
        const handlerName = `onWsResponse${this.capitalizeFirstLetter(response.messageId)}`;
        console.log({ handlerName })
        if (typeof this[handlerName] === "function") {
          this[handlerName].call(this, response.data);
        }
      } catch (ex) {
        console.error("Couldnt parse message.data")
      }
    },
  }
}