import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import HelloWorld from './components/HelloWorld'
import Pick from './components/Pick'
import Players from './components/Players'
import ShowError from './components/ShowError'
import MasterPicks from './components/MasterPicks'
import Spectator from './components/Spectator'
import PlayerOverview from './components/PlayerOverview'


Vue.config.productionTip = false

Vue.use(VueRouter)
Vue.component("players", Players)
Vue.component("show-error", ShowError)
Vue.component("master-picks", MasterPicks)
Vue.component("spectator", Spectator)
Vue.component("player-overview", PlayerOverview)

const router = new VueRouter({
  routes: [
    { path: '/', component: HelloWorld },
    { path: '/pick/:session', component: Pick },
    { path: '/pick/:session/:master', component: Pick }
  ]
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
