<template>
  <div class="base">
    <div class="column">
      <h3 class="my-team">My Picks ({{ (myPicks.reduce((acc, player) => (acc += player.skill), 0) / myPicks.length).toFixed(0) }})</h3>
      <div class="picks-box" :class="{ 'is-turn': isMyTurn }">
        <transition-group name="list" tag="div">
          <player-overview
            v-for="player in myPicks"
            :isMaster="masterIds.includes(player.name)"
            :key="player.name"
            :player="player"/>
        </transition-group>
      </div>
      <div class="turn-notifier blink" v-show="isMyTurn">It's your turn now!</div>
    </div>
    <div class="column">
      <h1 class="players">Players</h1>
      <div class="players-box">
        <player-overview
          v-for="player in poolPlayers"
          @picked="$emit('picked', $event)"
          :player="player"
          :skillRange="player.skillRange"
          :key="player.name"/>
      </div>
    </div>
    <div class="column">
      <h3 class="players">Opponent Picks ({{ (opponentPicks.reduce((acc, player) => (acc += player.skill), 0) / opponentPicks.length).toFixed(0) }})</h3>
      <div class="picks-box" :class="{ 'is-turn': !isMyTurn }">
        <transition-group name="list" tag="div">
          <player-overview
            v-for="player in opponentPicks"
            :isMaster="masterIds.includes(player.name)"
            :key="player.name"
            :player="player"/>
        </transition-group>
      </div>
      <div class="turn-notifier" v-show="!isMyTurn">Wait your opponent to pick...</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MasterPicks",
  props: {
    masterIds: {
      type: Array,
      default: () => ([])
    },
    myPicks: {
      type: Array,
      default: () => ([])
    },
    opponentPicks: {
      type: Array,
      default: () => ([])
    },
    poolPlayers: {
      type: Array,
      default: () => ([])
    },
    isMyTurn: Boolean
  }
}
</script>

<style>
  .base {
    display: flex;
    margin: 20px 60px; 
  }

  .base > .column {
    flex: 1;
  }

  .picks-box {
    border: 3px solid black;
    padding: 20px 0;
    min-height: 308px;
  }

  .picks-box.is-turn {
    background-color: #ade2ff;
  }

  .players-box {
    padding-top: 20px;
  }

  .turn-notifier {
    font-size: 12px;
    background: #61ff8a;
    color: black;
    padding: 8px 16px;
    margin-top: 4px;
  }

  .blink {
    -webkit-animation: flickerAnimation 1s infinite;
    -moz-animation: flickerAnimation 1s infinite;
    -o-animation: flickerAnimation 1s infinite;
    animation: flickerAnimation 1s infinite;
  }

  @keyframes flickerAnimation {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
  @-o-keyframes flickerAnimation{
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
  @-moz-keyframes flickerAnimation{
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
  @-webkit-keyframes flickerAnimation{
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }

  .list-enter-active, .list-leave-active {
    transition: all 1s;
  }
  .list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(30px);
  }
</style>