<template>
  <div>
    <h4>Select Active Players</h4>
    <div class="player-list">
      <div class="player-list__item"
           v-for="player in players"
           :key="player.name">
           <span>
             <input type="checkbox"
                    :checked="player.active"
                    :disabled="loading"
                    @change="onChange($event, player)"/>
           </span>
           <span>{{ player.name }}&nbsp;</span>
           <span>({{ player.skill }})</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Players",
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    playerList: {
      type: Array,
      default: () => ([])
    }
  },
  computed: {
    players() {
      if (!this.playerList) return []
      return [...this.playerList].sort((a,b) => (a.name.charAt(0) > b.name.charAt(0) ? 1 : -1))
    }
  },
  methods: {
    onChange(e, player) {
      this.$emit("playerSelect", {
        active: e.target.checked,
        player
      })
    }
  }
}
</script>

<style scoped>
.player-list {
  text-align: left;
  background-color: #6DCAFB;
  border-radius: 8px;
  padding: 10px;
}

.player-list__item {
  display: inline-block;
  margin: 8px;
  background-color: #9adcff;
  padding: 6px;
  border-radius: 8px;
}

.player-list__item input[type=checkbox] {
  margin-right: 8px;
  transform: scale(1.4);
}

.player-list__item input[type=checkbox][disabled] {
  opacity: 0.2;
}
</style>
