<template>
  <div>
    <show-error v-if="!sessionAvailable" message="Error: Session is no longer available"/>
    <master-picks v-if="isMaster && canSpectate"
                  :masterIds="masterIds"
                  :poolPlayers="pickPool"
                  :myPicks="myPicks"
                  :isMyTurn="isMyTurn"
                  :opponentPicks="opponentPicks"
                  @picked="onPicked"/>
    <spectator v-else-if="canSpectate"
                  :poolPlayers="spectatorProps.pickPool"
                  :myPicks="spectatorProps.firstTeam"
                  :opponentPicks="spectatorProps.secondTeam"/>
    <show-error v-else message="Error: Session information is incorrect"/>
  </div>
</template>

<script>
import wsmxn from "./WsMixin"

export default {
  name: "Pick",
  mixins: [wsmxn],
  data() {
    return {
      ws: null,
      isMaster: false,
      canSpectate: false,
      sessionAvailable: true,
      masterInfo: {},
      opponentMasterInfo: {},
      pickPool: [],
      myPicks: [],
      isMyTurn: false,
      opponentPicks: [],
      spectatorProps: {
        pickPool: [],
        firstTeam: [],
        secondTeam: []
      }
    }
  },
  computed: {
    masterIds() {
      if (!this.masterInfo.id || !this.opponentMasterInfo.id) {
        return []
      }
      return [this.masterInfo.id, this.opponentMasterInfo.id]
    }
  },
  methods: {
    onPicked(player) {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        this.activities.unshift("Ws error: not ready")
        return
      }
      const { master, session } = this.$route.params
      this.ws.send("picked;" + JSON.stringify({
        master,
        session,
        pickedPlayer: player
      }))
    },
    onWsOpen(event) {
      const { master: masterId, session: spectatorId } = this.$route.params
      console.log({ spectatorId })
      this.ws = event.target
      this.ws.send("masterConnected;" + JSON.stringify(masterId))
      this.ws.send("spectatorConnected;" + JSON.stringify(spectatorId))
      this.ws.send("sessionInfoForMaster;" + JSON.stringify(masterId))
    },
    onWsResponseMasterConnected(data) {
      console.log("onWsResponseMasterConnected", data)
      this.isMaster = data
    },
    onWsResponseSpectatorConnected(data) {
      console.log("onWsResponseSpectatorConnected", data)
      this.canSpectate = data
    },
    onWsResponseCreateSession(data) {
      if (data.newSessionCreated) {
        this.isMaster = false
        this.canSpectate = false
        this.sessionAvailable = false
      }
    },
    onWsResponseSessionInfoForMaster(data) {
      console.log("sessionInfoForMaster", data)
      const { master: masterId } = this.$route.params
      this.masterInfo = data.masters.find(m => m.id === masterId)
      this.opponentMasterInfo = data.masters.find(m => m.id !== masterId)
      // this.pickPool = [...data.pickPool]
    },
    onWsResponseSessionInfoUpdate(data) {
      console.log("onWsResponseSessionInfoUpdate")
      console.log("wololoo")
      if (this.masterInfo.player) {
        this.myPicks = data.picks[this.masterInfo.player.name]
        this.opponentPicks = data.picks[this.opponentMasterInfo.player.name]
        this.pickPool = data.pickPool    
        this.isMyTurn = data.turn.name === this.masterInfo.player.name
      } else {
        console.log("eeeelese")
        console.log(JSON.stringify(data.firstTeam, null, 2))
        console.log(JSON.stringify(data.secondTeam, null, 2))
        this.spectatorProps.pickPool = data.pickPool
        this.spectatorProps.firstTeam = data.firstTeam
        this.spectatorProps.secondTeam = data.secondTeam
      }
    },
    onWsResponsePicked(data) {
      console.log({ picked: JSON.stringify(data) })
    }
  },
  created() {
    this.connect()
  }
}
</script>

