<template>
  <div @click="onClick" :class="classes">
    <img v-if="isMaster" width="26" height="26" src="data:image/svg+xml;utf8;base64," />
    <span>{{ player.name }}</span>
  </div>
</template>

<script>
export default {
  name: "PlayerOverview",
  props: {
    player: {
      type: Object,
      default: () => ({
        name: "Unknown",
        skill: 0,
        pickable: false
      })
    },
    skillRange: {
      type: Number,
      default: 0
    },
    isMaster: Boolean
  },
  methods: {
    onClick() {
      if (this.player.pickable) {
        this.$emit("picked", this.player)
      }
    }
  },
  computed: {
    classes() {
      return {
        pickable: this.player.pickable,
        player: true,
        [`player skill-range-${this.skillRange}`]: true
      }
    }
  }
}
</script>

<style scoped>
  .player {
    border: 1px solid black;
    margin: 10px 16px;
    padding: 10px 16px;
    border-radius: 8px;
    opacity: 0.4;
    user-select: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .player > img {
    margin-right: 12px;
  }

  .player.skill-range-0 {
    background-color: white;
    color: black;
  }

  .player.skill-range-1 {
    background-color: #35E655;
    color: black;
    border-color: #0D7B21;
  }

  .player.skill-range-2 {
    background-color: #43A8E8;
    color: black;
    border-color: #43A8E8;
  }

  .player.skill-range-3 {
    background-color: #E8C043;
    color: black;
    border-color: #AB850B;
  }

  .pickable {
    opacity: 1;
  }
</style>