<template>
  <div>
    <div v-if="initial">
      <input 
        type="password"
        v-model="elpartido"
        @keydown.enter="onEnterPress"
        placeholder="password"/>
    </div>
    <div class="panel" v-else>
      <players :loading="loading" :playerList="playerList"  @playerSelect="onPlayerSelect"/>
      <button
        @click="onClickCurrentSessionInfo"
        :disabled="loading">Current Session Info</button>
      <button
        @click="onClickResetSession"
        :disabled="loading">New Random Session</button>
      <div class="create-session">
        <select v-model="selectedMaster0">
          <option v-for="player in playerList.filter(p => p.active)" :key="player.name">{{ player.name }}</option>
        </select>
        <select v-model="selectedMaster1">
          <option v-for="player in playerList.filter(p => p.active)" :key="player.name">{{ player.name }}</option>
        </select>
        <button
          @click="onClickCreateSessionWithSelectedMasters"
          :disabled="loading">Create New Session</button>
      </div>
      <div class="create-session">
        <input type="text" v-model="newPlayerName"/>
        <input type="number" v-model="newPlayerSkill" min="1" max="10"/>
        <button
          @click="onClickAddNewPlayer"
          :disabled="loading">Add New Player</button>
      </div>
    </div>
    <div class="activity-trace">
      <span v-for="(activity, index) in activities"
        :key="index"
        v-html="activity"/>
    </div>
  </div>
</template>

<script>
import wsmxn from "./WsMixin"

export default {
  name: "HelloWorld",
  mixins: [wsmxn],
  data() {
    return {
      elpartido: "",
      initial: true,
      loading: false,
      ws: null,
      activities: [],
      playerList: [],
      selectedMaster0: null,
      selectedMaster1: null,
      newPlayerName: "",
      newPlayerSkill: 1
    }
  },
  methods: {
    onEnterPress() {
      if (this.elpartido === "fwyd") {
        this.initial = false
      }
    },
    onPlayerSelect(data) {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        this.activities.unshift("Ws error: not ready")
        return
      }
      this.loading = true
      this.ws.send("playerActiveState;" + JSON.stringify(data))
    },
    onClickResetSession() {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        this.activities.unshift("Ws error: not ready")
        return
      }
      this.loading = true;
      this.ws.send("createSession")
      this.activities.unshift("Creating a new session")
    },
    onClickCreateSessionWithSelectedMasters() {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        this.activities.unshift("Ws error: not ready")
        return
      }
      console.log({
        m1: this.selectedMaster0,
        m2: this.selectedMaster1
      })
      if (!this.selectedMaster0 || !this.selectedMaster1) {
        return
      }
      this.ws.send("createSession;" + JSON.stringify({
        master0: this.selectedMaster0,
        master1: this.selectedMaster1
      }))
    },
    onClickAddNewPlayer() {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        this.activities.unshift("Ws error: not ready")
        return
      }
      if (this.newPlayerName.trim() === "") {
        return;
      }

      this.ws.send("createPlayer;" + JSON.stringify({
        name: this.newPlayerName,
        skill: this.newPlayerSkill
      }))
    },
    onClickCurrentSessionInfo() {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        this.activities.unshift("Ws error: not ready")
        return
      }
      this.loading = true;
      this.ws.send("sessionInfo")
      this.activities.unshift("Fetching current session info")
    },
    onWsOpen(event) {
      console.log("ws  is open now", event)
      this.ws = event.target
      this.ws.send("playerList")
    },
    createSessionLink(sessionId, masterId) {
      const url = `/#/pick/${sessionId}`
      if (!masterId) {
        return url
      }
      return `${url}/${masterId}`
    },
    showSessionInfo(session) {
      const master1Link = this.createSessionLink(session.id, session.masters[0].id)
      const master2Link = this.createSessionLink(session.id, session.masters[1].id)
      const spectatorLink = this.createSessionLink(session.id)
      this.activities.unshift(`<strong>${session.masters[0].player.name}</strong> is picking for Team 1. Url: <a href="${master1Link}" target="_blank">${master1Link}</a>`) 
      this.activities.unshift(`<strong>${session.masters[1].player.name}</strong> is picking for Team 2. Url: <a href="${master2Link}" target="_blank">${master2Link}</a>`)
      this.activities.unshift(`Spectator url: <a href="${spectatorLink}" target="_blank">${spectatorLink}</a>`)
    },
    onWsResponseCreateSession(session) {
      this.loading = false
      this.activities.unshift(`Created a new session with id ${session.id}`)
      console.log("onWsResponseCreateSession")
      console.log({ session })
      this.showSessionInfo(session)
      this.activities.unshift("---")
    },
    onWsResponseSessionInfo(session) {
      this.loading = false
      console.log({ session })
      this.showSessionInfo(session)
      this.activities.unshift("---")
    },
    onWsResponsePlayerList(playerList) {
      this.playerList = playerList
      console.log({ playerList })
      const activePlayers = playerList.filter(p => p.active)
      this.selectedMaster0 = activePlayers[0] || null
      this.selectedMaster1 = activePlayers[1] || null
    },
    onWsResponsePlayerActiveState(player) {
      console.log(player)
      this.loading = false
    }
  },
  mounted() {
    this.connect()
  },
  watch: {
    loading(/* value */) {
      // this.activities.unshift(value ? "Loading..." : "Loaded.")
    }
  }
}
</script>

<style scoped>
  .panel {
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: 0 10px;
  }
  .panel > button {
    margin-top: 4px;
  }
  .activity-trace {
    text-align: left;
    padding: 20px;
    width: 100vw;
  }
  .activity-trace > span {
    display: list-item;
    list-style-type: none;
    margin-bottom: 4px;
    font-size: 12px;
  }
  .create-session {
    display: flex;
    margin: 10px 0;
  }
  .create-session > * {
    flex: 1;
    margin: 0 6px;
  }
</style>
